.grid_row {
    display: grid;
    grid-template-columns: repeat(5, 1fr);

   
}

.grid_holder{
    display: grid;
    height: min(50vh, 50vw);
    width: min(50vh, 50vw);
    background-color: #192929;
}


.all_holder {
    display:flex;
    width:100%;
    height:100vh;
    justify-content: center;
    align-items: center;
}

#root{
    background-color: #192929;
}

@media (max-width: 550px){
    .grid_holder{
        display: grid;
        height: 100vw;
        width: 100vw;
        background-color: #192929;
    }
}