body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html{
  background-color: #192929;

}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.grid_row {
    display: grid;
    grid-template-columns: repeat(5, 1fr);

   
}

.grid_holder{
    display: grid;
    height: min(50vh, 50vw);
    width: min(50vh, 50vw);
    background-color: #192929;
}


.all_holder {
    display:flex;
    width:100%;
    height:100vh;
    justify-content: center;
    align-items: center;
}

#root{
    background-color: #192929;
}

@media (max-width: 550px){
    .grid_holder{
        display: grid;
        height: 100vw;
        width: 100vw;
        background-color: #192929;
    }
}
.grid_cell {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: gray;
    border-bottom: 5px solid rgba(0,0,0,.1);
    border-radius: 12%;
    font-size: larger;
    font-weight: 700;
    width:90%;
    height:80%;
    padding-top: 1%;
    color:white;
    text-transform: uppercase;
    
    transition: background-color 1s cubic-bezier(0.19,1,0.22,1),-webkit-transform .3s cubic-bezier(0.19,1,0.22,1);
    
    transition: transform .3s cubic-bezier(0.19,1,0.22,1),background-color 1s cubic-bezier(0.19,1,0.22,1);
    
    transition: transform .3s cubic-bezier(0.19,1,0.22,1),background-color 1s cubic-bezier(0.19,1,0.22,1),-webkit-transform .3s cubic-bezier(0.19,1,0.22,1);
}

.grid_cell.big_mode{
    width:120%;
    height:120%;
    /* background-color: red; */
}

.green {
    background-color: darkgreen !important;
}

.yellow {
    background-color: orange !important;
}

.grid_cell.dot {
    background-color: black;
    opacity:0;
}
